@font-face {
  font-family: 'Lato-Bold';
  src: url('./assets/fonts/Lato-Bold.ttf') format('truetype');
  font-weight: bold;
}

@font-face {
  font-family: 'Lato-Regular';
  src: url('./assets/fonts/Lato-Regular.ttf') format('truetype');
  font-weight: normal;
}

@font-face {
  font-family: 'Lato-LightItalic';
  src: url('./assets/fonts/Lato-LightItalic.ttf') format('truetype');
  font-style: italic;
  font-weight: 300;
}

/* Sayfanƒ±n genel stilleri */
.menu-page {
  background-color: #182840;
  min-height: 100vh;
  color: white;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Logo */
.menu-logo {
  max-width: 200px;
  height: auto;
  margin-bottom: 30px;
}

/* Men√º b√∂l√ºmleri */
.menu-section {
  margin: 30px auto;
  padding: 20px;
  width: 90%;
  max-width: 800px;
  font-size: 1.2rem;
  position: relative;
}

/* PNG Ayra√ß */
.menu-separator {
  width: 100%;
  max-width: 600px;
  height: auto;
  margin: 15px 0;
}

/* Ba≈ülƒ±klar (Lato-Bold) */
.menu-section h2 {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'Lato-Bold', sans-serif;
}

/* Men√º √∂ƒüeleri */
.menu-item {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 10px 0;
  font-size: 16px;
}

/* √úr√ºn isimleri (Lato-Regular) */
.menu-item span:first-child {
  font-family: 'Lato-Regular', sans-serif;
}

/* Fiyatlar (Lato-LightItalic) */
.menu-item span:last-child {
  font-family: 'Lato-LightItalic', sans-serif;
}

/* üåç Mobil ƒ∞√ßin Responsive Ayarlar */
@media (max-width: 768px) {
  .menu-logo {
    max-width: 220px !important;
    height: auto !important;
    margin-bottom: 25px !important;
  }

  .menu-section {
    width: 95%;
    padding: 15px;
    font-size: 14px;
  }

  .menu-section h2 {
    font-size: 20px;
  }

  .menu-item {
    font-size: 14px;
  }

  .menu-separator {
    max-width: 400px;
  }
}

@media (max-width: 480px) {
  .menu-logo {
    max-width: 250px !important;
    height: auto !important;
    margin-bottom: 20px !important;
  }

  .menu-section {
    width: 98%;
    padding: 10px;
  }

  .menu-section h2 {
    font-size: 18px;
  }

  .menu-item {
    font-size: 12px;
  }

  .menu-separator {
    max-width: 300px;
  }
}
